

body {
  font-family: 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  color: #2d2d2d;

}

.main-container {
  display: flex;
  flex-direction: row;
}
.container-bordered{
  border: 0.5px rgb(174, 174, 174) solid;
  border-radius: 20px;
  padding: 20px;
  margin: 30px;
}
.container {
  width: 100%;
}

.event-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5%;
  margin-bottom: 5%;
}

.sidebar {
  width: 80px;
  height: 100vh;
  background: #f7f5f8;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  position: relative;
  gap: 24px;
  overflow: hidden; /* Nasconde il contenuto quando la sidebar Ã¨ chiusa */
  top: 0;
  transform: translateX(-100%);
  transition: height 0.3s ease-in-out;
}

.sidebar.open {
  transform: translateX(0);
  padding: 20px 0;
  height: 40px;
}
@media only screen and (min-width: 940px){
  .sidebar.open{
    height: 100vh;
  }
}
@media only screen and (max-width: 940px) {
  .main-container {
  flex-direction: column;
  }
  .sidebar {
  flex-direction: row;
  justify-content: center;
  height: auto;
  width: 100%;
    padding: 15px 0;
  }
  #toggle-menu{
    display: block;
  }
}

/* footer */
.footer{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.icon-wrapper {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  transition: background 0.3s;
  cursor: pointer;
  color:#2d2d2d;
}

.icon-wrapper:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.icon-wrapper.active {
  background-color: #3d5afe;
  color: white;
}

.icon-wrapper img {
  width: 24px;
  height: 24px;
  filter: grayscale(100%) brightness(0.4);
}

.icon-wrapper.active img {
  filter: brightness(10) invert(1);
}

/* Header con logo e pulsanti */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e5e5e5;
}
.header #toggle-menu{
  display: none;
}
.header .logo {
  color: #f05537;
  font-weight: bold;
  font-size: 1.25rem;
}

.header .actions {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.actions button {
  height: 50px;
  padding: 0.5rem 0.5rem;
  border-radius: 40px;
  font-weight: bold;
}
.button-outline {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  border: 1px solid #f05537;
  color: #f05537;
  background-color: transparent;
  cursor: pointer;
  width: 100px;
}

.button-icon {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.button-user {
  display: flex;
  flex-direction: row;
  flex-wrap: none;
  justify-content:  space-between;
  align-items: center;
  column-gap: 13px;
  border: 1px solid white;
  background-color: rgb(233, 233, 233);
  cursor: pointer;
}

.avatar {
  background-color: #2237d2;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

@media only screen and (max-width: 840px) {
  .admin.header.username, .admin.header .button-outline .button-name{
    display: none;
  }
  .admin.header .button-outline{
    width: 50px;
    border-radius: 50%;
  }
   .admin.header #toggle-menu{
    display: block;
   }
}

.breadcrumb{
  display: flex;
  flex-direction: column;
  padding: 2rem;
}
h1{
  font-size: 50px;
}
@media only screen and (max-width: 780px){
  h1{
    font-size: 35px;
  }
}
/* Card profilo */
.profile-card {
  background: white;
  padding: 1rem;
  border-radius: 12px;
  margin-top: 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.profile-avatar {
  background-color: #e0e0e0;
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.profile-info h2 {
  margin: 0;
  font-size: 1.1rem;
}

.profile-info p {
  font-size: 0.9rem;
  color: #777;
}

/* Sezione evento */
.event-box {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  margin-top: 2rem;
}

.event-box h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.event-box h1 span {
  color: #3752f5;
}

.event-card {
  background-color: #f4f4f4;
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.event-card strong {
  display: block;
  margin-bottom: 0.25rem;
}

.event-card .on-sale {
  color: #d62828;
  font-weight: bold;
}

/* Pianificatore */
.planner {
  margin-top: 2rem;
}

.planner h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ddd;
  padding-bottom: 0.5rem;
}

.planner-entry {
  background: white;
  border-radius: 10px;
  padding: 1rem;
  margin-bottom: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.planner-date {
  text-align: right;
  min-width: 60px;
  font-weight: bold;
  color: #444;
}

.planner-content {
  flex: 1;
}

.planner-content h4 {
  margin: 0;
  font-size: 1rem;
}

.planner-content p {
  margin: 0.25rem 0 0;
  color: #666;
  font-size: 0.9rem;
}
/* tables */
.search-bar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.search-bar-button-wrapper{
  display: flex;
  flex-direction: row;
}
.search-input-wrapper{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  column-gap: 20px;
  align-items: center;
}
.search-bar input {
  width: 60%;
  padding-left: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
    height: 100%;
    min-width: 150px;
}

.search-bar button {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  column-gap: 8px;
  align-items: center;
  padding: 8px 15px;
  margin-left: 10px;
  border-radius: 20px;
  border: 0.5px grey solid;
  cursor: pointer;
  min-width: 120px;
  height: 40px;
}

@media only screen and (max-width: 840px) {
  .search-bar button{
    min-width: auto;
  }
  .button-label {
    display:none;
  }
}

.button-list {
  background-color: #4e4e4e;
  color: #fff;
}

.button-calendar {
  background-color: #fff;
  border: 1px solid #ccc;
}

.button-upcoming {
  background-color: #e6f0ff;
  border: none;
  color: #0066cc;
}

.button-create {
  background-color: #ff6347;
  color: #fff;
}

.table-container {
  padding: 20px;
  background-color: #fff;
}

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid none;
}

th, td {
  padding: 15px;
  text-align: left;
}

th {
  background-color: #f1f1f1;
}

.event-table td{
  color: rgb(167, 167, 167);
}
td.icon-wrapper{
  display: flex;
  flex-direction: row;
  align-items: center;
}
td.event-details-wrapper{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  column-gap: 30px;
  padding: 20px;
}
td.event-date{
  display: flex;
  flex-direction: column;
}
td .event-date .month{
  color: rgb(198, 4, 4);
  font-size: 15px;
}
td .event-date .day{
  font-size: 20px;
  color: black;
}

td .event-details{
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}
td .event-details .event-name{
  font-size: 15px;
  color: black;
}
td .event-details .event-date-time{
  font-size: 12px;
}
.status-on-sale {
  color: #4CAF50;
  font-weight: bold;
}

footer {
  padding: 20px;
  background-color: #f5f5f5;
}

.csv-export {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  width: 200px;
  padding: 10px 20px;
  background-color: none;
  color: rgba(0, 34, 255, 0.518);
  border: none;
  cursor: pointer;
}

/* ---- Event page ---- */
.event-card, .subscription-card{
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: solid none 1.5px;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 4px 8px 0 rgba(41, 94, 217, 0.2);
}

.subscribe-button{
  border: #f05537 1px solid;
  color: white;
  font-size: 20px;
  font-weight: bold;
  border-radius: 20px;
  background-color: #f05537;
  cursor: pointer;
  padding: 10px;
}

.subscription-form{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  row-gap: 20px;
  width: 100%;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="date"],
input[type="time"] {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}
input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
input[type="time"]:focus {
  border-color: #f05537;
  outline: none;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
.increment-button, .decrement-button{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  border: none;
  background-color: #f05537;
  cursor: pointer;
  color: white;
  font-size: 20px;
  font-weight: bold;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  box-shadow: 0 4px 8px 0 rgba(41, 94, 217, 0.2);
}
label.form-label{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  column-gap: 10px;
  font-size: 12px;
  font-weight: bold;
}
.form-group-row{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px 10px;
  width: 100%;
}
